.menu {
  position: fixed;
  top: 70px; // Adjust based on your topbar height
  right: -300px; // Hide off-screen initially
  width: 300px;
  height: calc(100vh - 70px);
  background-color: white;
  transition: all 0.3s ease-in-out;
  z-index: 2;

  &.active {
    right: 0;
  }

  ul {
    margin: 0;
    padding: 0;
    list-style: none;
    width: 60%;

    li {
      margin-bottom: 25px;

      a {
        font-size: 30px;
        color: inherit;
        text-decoration: none;
        font-weight: 300;

        &:hover {
          font-weight: 500;
        }
      }
    }
  }
}

// Adjust for mobile view if necessary
@media screen and (max-width: 768px) {
  .menu {
    width: 100%;
    right: -100%;

    ul {
      width: 100%;
      text-align: center;
    }
  }
}